
.about-wrap {
  padding: 20px 0;
}
.about-wrap ul{
  background-color: #fff;
  max-width: 1200px;
  min-width: 800px;
  margin: 0 auto;
  padding: 20px;
  border-radius: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.about-wrap ul li+li {
  margin-bottom: 20px;
}
.about-wrap ul li:first-child {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: bold;
  line-height: 50px;
  padding-bottom: 5px;
  border-bottom: 1px dotted #ccc;
}





